.myCalendarCss {
    max-width: 100%;
    border: none;
}

.myCalendarCss span.e-day:hover {
    color: #FFFFFF !important;
    background: #00BFA5 !important;
    border: 1px solid #00BFA5 !important;
}


.myCalendarCss .e-selected span.e-day {
    color: #00BFA5 !important;
    background: #FFFFFF !important;
    border: 1px solid #00BFA5 !important;
}

.myCalendarCss .e-focused-date span.e-day {
    color: #00BFA5 !important;
    background: #FFFFFF !important;
    border: 1px solid #00BFA5 !important;
}

.myCalendarCss .e-today span.e-day {
    color: #FFFFFF !important;
    border: 1px solid #00BFA5 !important;
    background: #00BFA5 !important;
}

.myCalendarCss .e-focused-date span.e-day:hover {
    color: #FFFFFF !important;
    background: #00BFA5 !important;
    border: 1px solid #00BFA5 !important;
}



.myCalendarCss thead.e-week-header tr>th {
    color: #00BFA5;
}